<template>
    <div id="rentDetailShare">
        <assistant :agent="apiData.simpleAgentModel" :callBigDataParams="getUvParamsString({ eventName : 2057002 })" />
       <download-app />
       <!---->
       <swiper :options="pageConfs.swiperOption">            
            <swiper-slide v-for="(slide , index) in apiData.simpleHouseRentDetailInfo.houseImageAndVideoList" :key="slide.imgKey">
                <video :src="slide.videoSrc" :poster="slide.imageSrc" controls="controls" preload="none"  class="img-responsive" style="height : 210px ; " v-if="slide.isVideo"></video>                
                <img :src="slide.imageSrc" class="img-responsive" v-else>
                <div class="pagination">{{ pageStates.swiperActiveIndex }} / {{ apiData.simpleHouseRentDetailInfo.houseImageAndVideoList.length }}</div>
            </swiper-slide>            
        </swiper>
       <!---->
       <div class="wk-panel summary">
            <div class="panel-header">
                <h1>{{ apiData.simpleHouseRentDetailInfo.subEstateName }}</h1>
                <div class="money">{{ apiData.simpleHouseRentDetailInfo.rentPrice }}  / </div>
            </div>
            <div  class="panel-body lr-padding tb-padding">
                <ul class="column-3">
                    <li><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.payTypeStr }}</dd></dl></li>
                    <li><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.houseTypeStr }}</dd></dl></li>
                    <li><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.spaceArea }} m&sup2;</dd></dl></li>
                </ul>
                <hr>
                <ul class="outline">
                    <li class="percent-70"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.houseChildTypeStr }}</dd></dl></li>
                    <li class="percent-30"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.renovationStr }}</dd></dl></li>
                    <li class="percent-70"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.floor }}/{{ apiData.simpleHouseRentDetailInfo.totalfloor }}</dd></dl></li>
                    <li class="percent-30"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.orientationStr }}</dd></dl></li>
                    <li class="percent-100"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.subEstateInitName }}</dd></dl></li>
                </ul>
                <hr>
                <ul class="subway-school">
                    <li><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.subwayStr }}</dd></dl></li>
                    <li><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.schoolStr }}</dd></dl></li>           
                </ul>
            </div>
        </div>
        <!---->
        <div class="wk-panel support top-gap">
            <div class="panel-header"></div>
            <ul  class="panel-body  lr-padding tb-padding">
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasBad }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasWardrobe }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasBalcony }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasHeating }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasGas }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasWashMachine }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasFridge }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasMicrowave }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasWaterHeater }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasAirConditioning }"></li>       
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasTV }"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasInternet }"></li>
            </ul>
        </div>
        <!---->
        <div class="wk-panel description top-gap">
            <div class="panel-header"></div>
            <div  class="panel-body  lr-padding tb-padding">
                <dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.sellPoint || "" }}</dd></dl>
                <dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.ownerMotivation || "" }}</dd></dl>
                <dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.aroundSupport || "" }}</dd></dl>
            </div>
        </div>
        <!---->
        <div class="wk-panel location top-gap">
            <div class="panel-header"></div>
            <div class="location-container">
                <img :src="'https://api.map.baidu.com/staticimage/v2?ak=GByG2pAz1WlmY7wX1rlIM4nd&width=640&height=426&center=' + apiData.simpleHouseRentDetailInfo.longitude + ',' + apiData.simpleHouseRentDetailInfo.latitude + '&zoom=18'" class="img-responsive">
                <div class="cover">
                    <p><i class="iconfont icon-dingwei"></i>{{ apiData.simpleHouseRentDetailInfo.subEstateInitName }}</p>            
                </div>
                <div class="mark"><i class="iconfont icon-dingwei"></i></div>
            </div>    
        </div>
        <!---->
    </div>    
</template>

<script>
    import downloadApp from "@/components/common/downloadApp" ;
    import assistant from "@/components/common/assistant" ;
    import { swiper , swiperSlide } from "vue-awesome-swiper" ;
    import apiDataFilter from "@/libraries/apiDataFilter" ;
    export default {
      name : "rentDetailShare" ,
      data () {
          return {
              pageStates : {
                  swiperActiveIndex : 1 //
              } ,
              pageConfs : {                                
                  swiperOption : {  //  swiper                     
                      name : "currentSwiper" ,
                      // Swiper
                      autoplay : 0 , //msslide  
                      grabCursor : true ,  //trueSwiper
                      setWrapperSize : true ,
                      autoHeight : false ,  //truewrappercontainerslide                      
                      //
                      onInit : swiper => {
                          this.pageStates.swiperActiveIndex = swiper.activeIndex + 1 ;
                      } ,
                      onSlideChangeEnd : swiper => {
                        this.pageStates.swiperActiveIndex = swiper.activeIndex + 1 ;
                      }       
                  }
              } ,
              apiData : {
                  simpleHouseRentDetailInfo : {} ,
                  simpleAgentModel : {}
              }            
          }
      } ,
      methods : {
          //
          getUvParamsString : function({ eventName , otherParams }) {
              let eventParam = { agent_id : this.$route.params.agentId , house_id : this.$route.params.houseId } ;
              if(otherParams !== undefined && otherParams !== null ) {
                  eventParam = Object.assign( eventParam , otherParams ) ;
              }              
              return encodeURIComponent(JSON.stringify({ 
                  eventName : eventName , 
                  eventParam : eventParam ,
                  type : 2
              })) ;
          }
      } ,      
      created() {          
          let houseId = this.$route.params.houseId ;
          let agentId = this.$route.params.agentId ;         
          apiDataFilter.request({
              apiPath : "rent.detail" ,
              data : { "houseId" : houseId , "agentId" : agentId } ,              
              successCallback : res => {                 
                  Object.assign(this.$data.apiData , res.body.data) ;
                  document.title = "" ;
                  //                  
                  let wechatShare = res.body.data.weChatShare ;
                  this.$wechatShare({
                      "title" : wechatShare.title ,
                      "timelineTitle" : wechatShare.timelineTitle ,
                      "content" : wechatShare.content ,
                      "imgUrl" : wechatShare.picUrl ,                      
                      "success" : function() { console.log("") ;  } ,
                      "fail" : function() { console.log("") ;  } ,
                      "cancel" : function() { console.log("") ; } ,
                      "complete" : function() { console.log("") ; }
                  }) ;
              }
          }) ;
          //
          this.$bigData({
            pageName : 2057 ,
            pageParam : {
                agent_id : agentId ,
                house_id : houseId
            } ,
            type : 1
          }) ;
      } ,
      components : {
          downloadApp ,
          assistant ,
          swiper ,
          swiperSlide
      }
    }
</script>

<style lang="less" scoped>
    @import "../../../../src/less/rent/detail.less" ;   
</style>


